import React, { useState, useEffect } from 'react';
import { 
    Alert,
    Grid,
    Box,
    Button,
    CircularProgress,
    Snackbar
} from '@mui/material';
import useAuth from 'app/hooks/useAuth';
import { styled } from '@mui/system';
import { useNavigate } from 'react-router-dom';
import { changePassword } from 'app/services/userService';
import { ValidatorForm, TextValidator, SelectValidator } from 'react-material-ui-form-validator';
import { ToastContainer, toast } from 'react-toastify';
import SessionModal from 'app/components/SessionModal/SessionModal';
import 'react-toastify/dist/ReactToastify.css';
import './Profile.css';

const TextField = styled(TextValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
}))

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
    marginTop: '8px'
}))

const CancelButton = styled(Button)(() => ({
    display: 'block',
    color: '#fff',
    marginTop: '20px',
    width: '86px',
    backgroundColor: '#e15260',
    '&:hover': {
        backgroundColor: '#cc2333',
        color: '#fff'
    }
}))

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    top: '6px',
    left: '25px',
}))


const ChangePassword = () => {
    const navigate = useNavigate();
    const { user } = useAuth();
    const [loading, setLoading] = useState(false);
    const [userInfo, setUserInfo] = useState({
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
    })
    const [open, setOpen] = useState(false);
    const [statusInfo, setStatusInfo] = useState({
        status: 'success',
        message: ''
    });
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');

    useEffect(() => {
        ValidatorForm.addValidationRule('valid_length', (value) => {
            if(value && value.length < 6) {
                return false;
            }
            return true;
        })

        ValidatorForm.addValidationRule('isMatch', (value) => {
            if(value && value !== userInfo.newPassword){
                return false;
            }
            return true;
        })

        return () => {
            ValidatorForm.removeValidationRule('valid_length');
            ValidatorForm.removeValidationRule('isMatch');
        }
    }, [userInfo]);

    const handleChange = ({ target: { name, value }}) => {
        const temp = {...userInfo};
        temp[name] = value;
        setUserInfo(temp);
    }

    const handleSubmit = () => {
        setLoading(true);
        const {
            oldPassword,
            newPassword
        } = userInfo;

        const data = {
            oldPassword,
            newPassword
        };

        changePassword(data)
        .then(resp => {
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } 
            if(resp && resp.status === 'success') {
                setLoading(false);
                setOpen(true);
                setStatusInfo({ 
                    ...statusInfo, 
                    status: resp && resp.status === 'success' ? 'success' : 'error', 
                    message: resp?.message 
                });
                navigate(-1);
            } else if(resp && resp.status === 'fail') {
                setLoading(false);
                setOpen(true);
                setStatusInfo({ 
                    ...statusInfo, 
                    status: resp && resp.status === 'success' ? 'success' : 'error', 
                    message: resp?.message 
                });
            } else {
                setLoading(false)
                setOpen(true);
                setStatusInfo({ ...statusInfo, status: 'error', message: 'Something went wrong'});
            }
        }).catch(err => {
            setLoading(false)
            setOpen(true);
            setStatusInfo({ ...statusInfo, status: 'error', message: 'Something went wrong'});
        })
    }

    const {
        oldPassword,
        newPassword,
        confirmPassword
    } = userInfo;

    return (
        <div className="EditProfile-container">
            <div className='Profile-header'>
                <h1 className="Profile-heading">Change Password</h1>
            </div>
            <div className="Profile-card">
                <ValidatorForm
                        onSubmit={handleSubmit}
                        onError={() => null}
                    >
                    <Grid container rowSpacing={2} columnSpacing={6}>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <TextField
                                margin="dense"
                                id="oldPassword"
                                name="oldPassword"
                                label="Old Password*"
                                type="password"
                                value={oldPassword}
                                onChange={handleChange}
                                fullWidth
                                validators={['required']}
                                errorMessages={['This field is required']}
                            />
                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <TextField
                                margin="dense"
                                id="newPassword"
                                name="newPassword"
                                label="New Password*"
                                type="password"
                                value={newPassword}
                                onChange={handleChange}
                                fullWidth
                                validators={['required', 'valid_length']}
                                errorMessages={['This field is required', 'Password must be atleast 6 characters']}
                            />
                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <TextField
                                margin="dense"
                                id="confirmPassword"
                                name="confirmPassword"
                                label="Confirm Password*"
                                type="password"
                                value={confirmPassword}
                                onChange={handleChange}
                                fullWidth
                                validators={['required', 'isMatch']}
                                errorMessages={['This field is required', 'Password Do Not Match!']}
                            />
                        </Grid>
                    </Grid>
                    <div
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: '1rem',
                        }}
                    >
                        <Box
                            position="relative"
                            sx={{ display: 'block', marginTop: '20px' }}
                        >
                            <Button
                                disabled={loading}
                                color="primary"
                                variant="contained"
                                type="submit"
                            >
                                Save
                            </Button>
                            {loading && (
                                <StyledProgress
                                    size={24}
                                    className="buttonProgress"
                                />
                            )}
                        </Box>
                        <CancelButton onClick={() => navigate(-1)}>
                            Cancel
                        </CancelButton>
                    </div>
                </ValidatorForm>
            </div>
            <Snackbar
                open={open}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setOpen(false)
                    if(statusInfo.status === 'success'){
                        navigate(-1)
                    }
                }}
            >
                <Alert
                    onClose={() => {
                        setOpen(false)
                        if(statusInfo.status === 'success'){
                            navigate(-1)
                        }
                    }}
                    variant="filled"
                    severity={statusInfo.status}
                    sx={{ width: '100%' }}
                >
                    {statusInfo.message}
                </Alert>
            </Snackbar>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </div>
    )
}

export default ChangePassword;
